import React from 'react';
import { t } from 'utils';
import browser from 'webextension-polyfill';
import * as S from './styles';

type Props = {
  isSuccess: boolean;
  openPlantEditDialog: (isPlanting: boolean) => void;
};

export default function ResultButtons({
  isSuccess,
  openPlantEditDialog,
}: Props) {
  const link = `https://www.forestapp.cc/sharings/planting_${
    isSuccess ? 'success' : 'failed'
  }`;
  const fbShareLink = `https://www.facebook.com/sharer/sharer.php?u=${link}`;
  const twitterShareMsg = isSuccess
    ? t('main_share_on_success')
    : t('main_share_on_failed');
  const twitterShareLink = `https://twitter.com/intent/tweet?text=${twitterShareMsg}&url=${link}`;

  return (
    <S.Box>
      <S.IconBtn
        src={require('popup/images/circle-edit-btn.png')}
        onClick={() => openPlantEditDialog(false)}
      />
      <S.IconBtn
        src={require('popup/images/twitter-btn.svg')}
        onClick={() => browser.tabs.create({ url: twitterShareLink })}
      />
      <S.IconBtn
        src={require('popup/images/facebook-btn.svg')}
        onClick={() => browser.tabs.create({ url: fbShareLink })}
      />
    </S.Box>
  );
}
