import React, { useEffect, useState } from 'react';
import * as storage from 'storage';
import type { PlantingStatus, SiteBlockingMode } from 'types';
import { getCurrentTabUrl, sendMessage, sendTabsMessage, t } from 'utils';
import browser from 'webextension-polyfill';
import AddSiteButton from '../AddSiteButton';
import * as S from './styles';

type Props = {
  backToIdle: () => void;
  inititalSiteList: string[];
  initialSiteBlockingMode: SiteBlockingMode;
  plantingStatus: PlantingStatus;
  openPlantEditDialog: (isPlanting: boolean) => void;
};

export default function TopRow({
  backToIdle,
  inititalSiteList,
  initialSiteBlockingMode,
  plantingStatus,
  openPlantEditDialog,
}: Props) {
  const [siteList, setSiteList] = useState(inititalSiteList);
  const [siteBlockingMode, setSiteBlockingMode] = useState(
    initialSiteBlockingMode
  );
  const [isSiteInList, setIsSiteInList] = useState<null | boolean>(null);

  useEffect(() => {
    (async function onSiteListChange() {
      const currentURL = await getCurrentTabUrl();
      setIsSiteInList(siteList.some((url) => currentURL.includes(url)));
    })();
  }, [siteList]);

  const changeSiteBlockingMode = async () => {
    const newMode: SiteBlockingMode =
      siteBlockingMode === 'Allow' ? 'Block' : 'Allow';
    setSiteBlockingMode(newMode);
    sendTabsMessage({
      type: 'UPDATE_CONTENT_SCRIPT_SITE_BLOCKING_MODE',
      payload: newMode,
    });
    storage.setData('siteBlockingMode', newMode);
    const siteList = await storage.getData(
      newMode === 'Allow' ? 'allowList' : 'blockList'
    );
    setSiteList(siteList);
  };

  const addSiteToList = async () => {
    if (isSiteInList) return;
    const currentURL = await getCurrentTabUrl();
    const newSiteList = [...siteList, currentURL];
    setSiteList(newSiteList);
    storage.setData(
      siteBlockingMode === 'Allow' ? 'allowList' : 'blockList',
      newSiteList
    );
    sendMessage({
      type: 'OPTIONS_PAGE_REVALIDATE_SITE_LIST',
      payload: {
        siteBlockingMode,
        siteList: newSiteList,
      },
    });
  };

  const getBtnChangeModeToolTipMsg = () => {
    if (plantingStatus === 'Started') {
      return t('extension_cannot_edit');
    }

    switch (siteBlockingMode) {
      case 'Allow':
        return t('extension_whitelist_mode_hover');
      case 'Block':
        return t('extension_blacklist_mode_hover');
    }
  };

  return (
    <S.TopRow>
      {(function TopLeftBtn() {
        switch (plantingStatus) {
          case 'Idle':
            return (
              <S.Group>
                <S.IconBtn
                  src={require('popup/images/settings.svg')}
                  onClick={() => browser.runtime.openOptionsPage()}
                />
                <S.ToolTip dir="right">{t('main_settings_tooltip')}</S.ToolTip>
              </S.Group>
            );
          case 'Started':
            return (
              <S.Group>
                <S.IconBtn
                  src={require('popup/images/edit.svg')}
                  onClick={() => openPlantEditDialog(true)}
                />
                <S.ToolTip dir="right">{t('main_edit_tooltip')}</S.ToolTip>
              </S.Group>
            );
          case 'Succeeded':
          case 'Failed':
            return (
              <S.IconBtn
                src={require('popup/images/return.svg')}
                onClick={backToIdle}
              />
            );
        }
      })()}
      <S.BtnChangeMode
        disabled={plantingStatus === 'Started'}
        onClick={changeSiteBlockingMode}
      >
        {siteBlockingMode === 'Block' && t('extension_blacklist_mode')}
        {siteBlockingMode === 'Allow' && t('extension_whitelist_mode')}
        <S.ToolTip dir="bottom">{getBtnChangeModeToolTipMsg()}</S.ToolTip>
      </S.BtnChangeMode>
      {(function TopRightBtn() {
        if (plantingStatus === 'Idle') {
          return isSiteInList === null ? (
            <S.EmptyIcon />
          ) : (
            <AddSiteButton
              isSiteInList={isSiteInList}
              addSiteToList={addSiteToList}
              siteBlockingMode={siteBlockingMode}
            />
          );
        }
        return <S.EmptyIcon />;
      })()}
    </S.TopRow>
  );
}
