import { SpringValue } from '@react-spring/web';
import useAsyncInitState from 'hooks/useAsyncInitState';
import { calcCoins } from 'popup/helpers';
import React from 'react';
import * as storage from 'storage';
import { t } from 'utils';
import browser from 'webextension-polyfill';
import * as S from './styles';

type Props = {
  close: () => void;
  style: { opacity: SpringValue<number>; transform: SpringValue<string> };
};

export default function SuccessDialog({ style, close }: Props) {
  const [state] = useAsyncInitState<{
    status: 'NoAccount' | 'NoAccountCTA' | 'NoBoost' | 'Boost' | 'Unknown';
    coins: number;
  }>({ status: 'Unknown', coins: 0 }, async (set) => {
    const user = await storage.getData('user');

    if (user === null) {
      const canShowNoAccountCTA = await storage.getData('canShowNoAccountCTA');
      return set({
        status: canShowNoAccountCTA ? 'NoAccountCTA' : 'NoAccount',
        coins: 0,
      });
    }

    const { resultPlant, growingPlant } = await storage.get([
      'resultPlant',
      'growingPlant',
    ]);
    const plant = (growingPlant || resultPlant)!;
    const plantingDuration = Math.floor(
      (new Date(plant.endTime).getTime() -
        new Date(plant.startTime).getTime()) /
        1000
    );
    const { boostEndTime } = user;
    const coins = calcCoins(plantingDuration);

    if (boostEndTime === null) return set({ status: 'NoBoost', coins });
    if (new Date(boostEndTime) < new Date())
      return set({ status: 'NoBoost', coins });

    return set({ status: 'Boost', coins });
  });

  const { status } = state;
  if (status === 'Unknown') return null;
  if (status === 'NoAccount') return null;

  return (
    <S.Overlay
      onClick={({ target, currentTarget }) =>
        target === currentTarget && close()
      }
      style={{ opacity: style.opacity }}
    >
      <S.Dialog
        style={{ transform: style.transform }}
        $forGuest={status === 'NoAccountCTA'}
      >
        {status === 'NoAccountCTA' ? (
          <>
            <S.GuestMessage>
              {t('main_grown_popup_guest_info_pre')}{' '}
              {t('main_grown_popup_guest_info_link')}{' '}
              {t('main_grown_popup_guest_info_post')}
              <S.SmallCoin src={require('popup/images/coin.svg')} />
            </S.GuestMessage>
            <S.GuestButtons>
              <S.BtnNextTime onClick={close}>{t('next_time')}</S.BtnNextTime>
              <S.BtnLearnMore
                onClick={() => {
                  browser.tabs.create({ url: 'https://forestapp.cc' });
                  close();
                }}
              >
                {t('learn_more')}
              </S.BtnLearnMore>
            </S.GuestButtons>
            <S.DontShowAgain
              onClick={() => {
                storage.set({ canShowNoAccountCTA: false });
                close();
              }}
            >
              {t('main_grown_popup_guest_do_not_show_again')}
            </S.DontShowAgain>
          </>
        ) : (
          <>
            <S.Title>{t('main_grown_popup_user_reward_pre')}</S.Title>
            <S.Reward>
              <S.Coin src={require('popup/images/coin.svg')} />
              <S.CoinReward>{state.coins}</S.CoinReward>
              {status === 'Boost' && (
                <S.CoinRewardBoosted>+{state.coins * 2}</S.CoinRewardBoosted>
              )}
            </S.Reward>
            <S.Confirm onClick={close}>OK</S.Confirm>
          </>
        )}
      </S.Dialog>
    </S.Overlay>
  );
}
