import { Port } from 'const';
import useAsyncInitState from 'hooks/useAsyncInitState';
import useDialogTrans from 'hooks/useDialogTrans';
import useItemDialogTrans from 'hooks/useItemDialogTrans';
import type { Message } from 'messages';
import React, { useEffect, useLayoutEffect, useState } from 'react';
import { createRoot } from 'react-dom/client';
import * as storage from 'storage';
import type {
  GrowingPlant,
  L10nKey,
  Plant,
  PlantingStatus,
  PopupProps,
} from 'types';
import { postMessage, sendMessage, t } from 'utils';
import browser from 'webextension-polyfill';
import PlantEditDialog from './components/PlantEditDialog';
import ResultButtons from './components/ResultButtons';
import SuccessDialog from './components/SuccessDialog';
import Timer from './components/Timer';
import TopRow from './components/TopRow';
import * as S from './styles';

type Props = {
  initialProps: PopupProps;
  port: browser.Runtime.Port;
};

function Popup({ initialProps, port }: Props) {
  const [isMac] = useAsyncInitState(false, async (set) =>
    set((await browser.runtime.getPlatformInfo()).os === 'mac')
  );
  const [plantingStatus, setPlantingStatus] = useState<PlantingStatus>(
    initialProps.plantingStatus
  );
  const [timeRemaining, setTimeRemaining] = useState(
    initialProps.timeRemaining
  );
  const isPlanting = plantingStatus === 'Started';
  const [plant, setPlant] = useState(initialProps.plant);
  const [plantMessageId, setPlantMessageId] = useState(0);
  const successDialog = useDialogTrans();
  const plantEditDialog = useItemDialogTrans<GrowingPlant | Plant>();

  useEffect(() => {
    if (plantingStatus === 'Succeeded') {
      plantEditDialog.off();
      successDialog.on();
    }
  }, [plantingStatus]);

  useEffect(() => {
    port.onMessage.addListener((message: Message) => {
      switch (message.type) {
        case 'UPDATE_PLANT':
          return setPlant(message.payload);
        case 'UPDATE_PLANT_AND_TIME_REMAINING':
          setTimeRemaining(message.payload.timeRemaining);
          setPlant(message.payload.plant);
          return;
      }
    });
    browser.runtime.onMessage.addListener((message: Message) => {
      switch (message.type) {
        case 'UPDATE_PLANTING_STATUS':
          return setPlantingStatus(message.payload);
      }
    });
  }, []);

  const openPlantEditDialog = async (isPlanting: boolean) => {
    const plant = await storage.getData(
      isPlanting ? 'growingPlant' : 'resultPlant'
    );
    plantEditDialog.on(plant!);
  };

  const startPlanting = (plantId: number) =>
    postMessage(port, {
      type: 'START_PLANTING',
      payload: { plantId },
    });

  const backToIdle = () =>
    postMessage(port, { type: 'BACK_TO_IDLE', payload: null });

  const getDescription = () => {
    switch (plantingStatus) {
      case 'Idle':
        return t('main_top_description_ready');
      case 'Succeeded':
        return t('main_top_description_grown');
      case 'Failed':
        return t('main_top_description_failed');
      case 'Started':
        return t(`main_message_growing_text_${plantMessageId}` as L10nKey);
    }
  };

  useLayoutEffect(() => {
    if (timeRemaining % 10 === 0 && plantingStatus === 'Started') {
      setPlantMessageId(Math.floor(Math.random() * 13));
    }
  }, [timeRemaining, plantingStatus]);

  return (
    <>
      <S.GlobalStyle isMac={isMac} />
      <S.Popup>
        <TopRow
          backToIdle={backToIdle}
          plantingStatus={plantingStatus}
          inititalSiteList={initialProps.siteList}
          initialSiteBlockingMode={initialProps.siteBlockingMode}
          openPlantEditDialog={openPlantEditDialog}
        />
        <S.Description>{getDescription()}</S.Description>
        <S.PlantRow disabled={plantingStatus !== 'Idle'}>
          <S.PlantArrow
            src={require('popup/images/left-arrow.svg')}
            onClick={() =>
              postMessage(port, { type: 'MODIFY_PLANT_INDEX', payload: 'Prev' })
            }
          />
          <S.PlantBall
            isLocked={plant.isLocked}
            {...(plant.isLocked === false && {
              onClick: () => startPlanting(plant.id),
            })}
          >
            <S.Plant
              src={plant.image}
              isLocked={plant.isLocked}
              onContextMenu={(e) => e.preventDefault()}
            />
            {plant.isLocked && (
              <S.Lock src={require('popup/images/lock.svg')} />
            )}
          </S.PlantBall>
          <S.PlantArrow
            src={require('popup/images/right-arrow.svg')}
            onClick={() =>
              postMessage(port, { type: 'MODIFY_PLANT_INDEX', payload: 'Next' })
            }
          />
        </S.PlantRow>
        {(plantingStatus === 'Idle' || plantingStatus === 'Started') && (
          <Timer
            port={port}
            canModify={plantingStatus === 'Idle'}
            timeInSeconds={timeRemaining}
          />
        )}
        {(plantingStatus === 'Failed' || plantingStatus === 'Succeeded') && (
          <ResultButtons
            isSuccess={plantingStatus === 'Succeeded'}
            openPlantEditDialog={openPlantEditDialog}
          />
        )}
        {successDialog.trans(
          (style, canShow) =>
            canShow && <SuccessDialog style={style} close={successDialog.off} />
        )}
        {plantEditDialog.trans(
          ({ opacity, transform }, plant) =>
            plant && (
              <PlantEditDialog
                plant={plant}
                isPlanting={isPlanting}
                close={plantEditDialog.off}
                dialogTransform={transform}
                overlayOpacity={opacity}
              />
            )
        )}
      </S.Popup>
    </>
  );
}

(async function start() {
  const port = browser.runtime.connect({ name: Port.Popup });
  const props = await sendMessage({ type: 'GET_POPUP_PROPS', payload: null });
  createRoot(document.getElementById('root')!).render(
    <Popup initialProps={props} port={port} />
  );
})();
