import React from 'react';
import type { TimeModifyMethod } from 'types';
import { postMessage, secondsToTime } from 'utils';
import browser from 'webextension-polyfill';
import * as S from './styles';

type Props = {
  port: browser.Runtime.Port;
  timeInSeconds: number;
  canModify: boolean;
};

export default function Timer({ port, timeInSeconds, canModify }: Props) {
  const modifyTargetTime = (method: TimeModifyMethod) => () =>
    postMessage(port, { type: 'MODIFY_TIME', payload: { method } });

  return (
    <S.TimeRow $disabled={canModify === false}>
      <S.Arrow
        src={require('popup/images/left-arrow.svg')}
        onClick={modifyTargetTime('Decrease')}
      />
      <S.Time>{secondsToTime(timeInSeconds)}</S.Time>
      <S.Arrow
        src={require('popup/images/right-arrow.svg')}
        onClick={modifyTargetTime('Increase')}
      />
    </S.TimeRow>
  );
}
