import { SpringValue } from '@react-spring/web';
import useAsyncInitState from 'hooks/useAsyncInitState';
import React, { useState } from 'react';
import * as storage from 'storage';
import { GrowingPlant, Plant, PlantRecord, Tag } from 'types';
import { t } from 'utils';
import * as S from './styles';

type Props = {
  close: () => void;
  plant: Plant | GrowingPlant;
  isPlanting: boolean;
  overlayOpacity: SpringValue<number>;
  dialogTransform: SpringValue<string>;
};

export default function PlantEditDialog({
  plant,
  isPlanting,
  close,
  overlayOpacity,
  dialogTransform,
}: Props) {
  const [tags] = useAsyncInitState<Tag[]>([], async (set) => {
    const tags = await storage.getData('tags');
    set(tags);
  });
  const [tag, setTag] = useState(plant.tag);
  const [note, setNote] = useState(plant.note);

  const save = async () => {
    const newPlant = { ...plant, tag, note };

    if (isPlanting) {
      await storage.set({ growingPlant: newPlant });
    } else {
      await Promise.all([
        storage.set({ resultPlant: newPlant as Plant }),
        storage.setData('plantRecords', (records) =>
          records.map((record) =>
            record.plant.startTime === plant.startTime
              ? ({
                  ...record,
                  plant: newPlant,
                  syncStatus: 'Pending',
                } as PlantRecord)
              : record
          )
        ),
      ]);
    }

    close();
  };

  return (
    <S.Overlay style={{ opacity: overlayOpacity }}>
      <S.Dialog style={{ transform: dialogTransform }}>
        <S.TagSelect
          value={tag}
          onChange={({ target }) => setTag(parseInt(target.value))}
        >
          {tags.map(({ tagId, title }) => (
            <option key={tagId} value={tagId}>
              {title}
            </option>
          ))}
        </S.TagSelect>
        <S.Textarea
          rows={6}
          cols={24}
          value={note}
          onChange={({ target }) => setNote(target.value)}
        ></S.Textarea>
        <S.ButtonsGroup>
          <S.BtnCancel onClick={close}>
            {t('main_edit_popup_cancel')}
          </S.BtnCancel>
          <S.BtnConfirm onClick={save}>{t('main_edit_popup_btn')}</S.BtnConfirm>
        </S.ButtonsGroup>
      </S.Dialog>
    </S.Overlay>
  );
}
