import { useTransition } from '@react-spring/web';
import React from 'react';
import { SiteBlockingMode } from 'types';
import { t } from 'utils';
import { ToolTip } from '../TopRow/styles';
import * as S from './styles';

type Props = {
  isSiteInList: boolean;
  addSiteToList: () => Promise<void>;
  siteBlockingMode: SiteBlockingMode;
};

export default function AddSiteButton({
  isSiteInList,
  addSiteToList,
  siteBlockingMode,
}: Props) {
  const trans = useTransition(isSiteInList, {
    from: { opacity: 0 },
    enter: { opacity: 1 },
    leave: { opacity: 0 },
  });

  return (
    <S.Button onClick={addSiteToList}>
      {trans((style, isSiteInList) =>
        isSiteInList ? (
          <S.Icon
            style={style}
            src={require('popup/images/check.svg')}
            alt="Done"
          />
        ) : (
          <S.Icon
            style={style}
            src={require('popup/images/add.svg')}
            alt="Add"
            $hasToolTip
          />
        )
      )}
      <ToolTip dir="left">
        {siteBlockingMode === 'Allow'
          ? t('main_add_whitelist_tooltip')
          : t('main_add_blacklist_tooltip')}
      </ToolTip>
    </S.Button>
  );
}
